import React, { useEffect } from "react";
import "./services-page.styles.scss";
import Fade from "react-reveal/Fade";
import StandardServices from "../../sections/standard-services/standard-services.component";
import OptionalServices from "../../sections/optional-services/optional-services.component";
import servicesBkg from "../../assets/jpg/services-bkg.jpg";
import Preload from "react-preload";
const loadingIndicator = <div className="preloading-loading" />;
const images = [servicesBkg];

const ServicesPage = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <section className="services-page-container">
      <Preload
        loadingIndicator={loadingIndicator}
        images={images}
        autoResolveDelay={3000}
        resolveOnError={true}
        mountChildren={true}
      >
        <Fade>
          <div className="services-header">
            <Fade left>
              <h2 className="services-title">Services</h2>
            </Fade>
          </div>
        </Fade>
        <StandardServices />
        <OptionalServices />
      </Preload>
    </section>
  );
};

export default ServicesPage;
